/* Modals */
.modal_container_1,
.modal_container_2,
.modal_container_3,
.modal_container_4,
.modal_container_5,
.modal_container_6 {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  opacity: 0;
  transition: ease-in-out 0.33s;
}

.modal_active {
  opacity: 1;
  pointer-events: auto;
}

.modal_1,
.modal_2,
.modal_3,
.modal_4,
.modal_5,
.modal_6 {
  background-color: #f6f7ff;
  max-width: 100%;
  width: 60%;
  height: 90%;
  border-radius: 5px;
  overflow: scroll;
  position: relative;
}

.modal_heading,
.modal_list,
.p_modal {
  text-align: center;
  color: #14272f;
}

.modal_section {
  display: flex;
  padding: 15px;
  opacity: 0.7;
}

.modal_text,
.modal_list {
  width: 50%;
}

.modal_list li {
  list-style: none;
}

.modal_buttons {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  padding-left: 15px;
}

.content_buttons {
  margin-top: 10px;
}

.content_buttons a {
  margin-left: 15px;
}

.modal_close_button {
  margin-right: 15px;
}

.close {
  padding: 0px;
}

.a-button-modal {
  color: #14272f;
}

.a-button-modal:hover {
  color: #0575e6;
  text-decoration: underline;
  transition: 0.3s ease;
}

.x_modal {
  position: absolute;
  top: 10px;
  right: 20px;
  font-weight: bold;
  color: #f6f7ff;
  cursor: pointer;
}

.x_modal4,
.x_modal5,
.x_modal6 {
  color: #14272f;
}

/* MODAL PICS*/
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

/* Next & previous buttons */
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: #0575e6;
  font-weight: bold;
  font-size: 18px;
  transition: 0.3s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}

.prev:hover,
.next:hover {
  transform: scale(1.3);
}

.modal_text p {
  color: #14272f;
}

/* Position the "next button" to the right */
.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #0575e6;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
@media (max-width: 800px) {
  .modal_1,
.modal_2,
.modal_3,
.modal_4,
.modal_5,
.modal_6 {
